import { useMemo } from 'react';
import useImage from 'use-image';
import { Image } from 'react-konva';
import SantaChest from '../images/1_2.png';
import ElfChest from '../images/2_2.png';
import RudolfChest from '../images/3_2.png';
import SnowmaidenChest from '../images/4_2.png';


const Chest = ({ currentChest, x, y }) => {
    const chest = useMemo(() => {
        if (currentChest === 'santa') return SantaChest;
        if (currentChest === 'elf') return ElfChest;
        if (currentChest === 'rudolf') return RudolfChest;
        if (currentChest === 'snowmaiden') return SnowmaidenChest;
    }, [currentChest]);

    const [image] = useImage(chest);

    const chestX = useMemo(() => {
        if (currentChest === 'santa') return x;
        if (currentChest === 'elf') return x;
        if (currentChest === 'rudolf') return x;
        if (currentChest === 'snowmaiden') return x;
    },[currentChest, x]);

    const chestY = useMemo(() => {
        if (currentChest === 'santa') return y;
        if (currentChest === 'elf') return y;
        if (currentChest === 'rudolf') return y - 1;
        if (currentChest === 'snowmaiden') return y + 1;
    },[currentChest, y]);

    const chestWidth = useMemo(() => {
        if (currentChest === 'santa') return 96;
        if (currentChest === 'elf') return 96;
        if (currentChest === 'rudolf') return 96;
        if (currentChest === 'snowmaiden') return 96;
    },[currentChest]);

    const chestHeight = useMemo(() => {
        if (currentChest === 'santa') return 70;
        if (currentChest === 'elf') return 62;
        if (currentChest === 'rudolf') return 52;
        if (currentChest === 'snowmaiden') return 72;
    },[currentChest]);
    
    return (
      currentChest &&
        <Image x={chestX} y={chestY} width={chestWidth} height={chestHeight} image={image} />
    )
};

export { Chest };
