import '../App.css';
import SantaHat from '../images/1_1.png';
import SantaChest from '../images/1_2.png';
import SantaLegs from '../images/1_3.png';
import ElfHat from '../images/2_1.png';
import ElfChest from '../images/2_2.png';
import ElfLegs from '../images/2_3.png';
import RudolfHat from '../images/3_1.png';
import RudolfChest from '../images/3_2.png';
import RudolfLegs from '../images/3_3.png';
import SnowMaidenHat from '../images/4_1.png';
import SnowMaidenChest from '../images/4_2.png';
import SnowMaidenLegs from '../images/4_3.png';
import { useState } from 'react';
import { ACTION_TYPE } from '../reducer';

const Menu = ({ dispatch }) => {
  const [subMenu, setSubMenu] = useState(null);

  const onClick = (type, name) => (el) => {
    if (type === 'hat') {
      dispatch({ type: ACTION_TYPE.SET_CURRENT_HAT, payload: name });
    } else if (type === 'chest') {
      dispatch({ type: ACTION_TYPE.SET_CURRENT_CHEST, payload: name });
    } else {
      dispatch({ type: ACTION_TYPE.SET_CURRENT_LEGS, payload: name });
    }

    if (name === 'santa') {
      dispatch({ type: ACTION_TYPE.SET_CURRENT_EMOTION, payload: 'positive' });
    } else {
      dispatch({ type: ACTION_TYPE.SET_CURRENT_EMOTION, payload: 'negative' });
    }

    setSubMenu(null);
  };

  const onMenuItemClick = (type) => () => {
    setSubMenu(subMenu !== type ? type : null);
  }

  return (
    <div className={'menu_container'}>
      <div className={'menu_wrapper'}>
        <div
          className={'menu_sub sub1'}
          style={{
            display: subMenu === 'sub1' ? 'block' : 'none'
          }}
        >
          <div className={'menu_subitem'}>
            <img
              onClick={onClick('hat', 'santa')}
              src={SantaHat}
              className={'menu_thumb'}
              alt={'santa_hat'}
            />
          </div>
          <div className={'menu_subitem'}>
            <img
              src={ElfHat}
              alt={'elf_hat'}
              className={'menu_thumb'}
              onClick={onClick('hat', 'elf')}
            />
          </div>
          <div className={'menu_subitem'}>
            <img
              src={RudolfHat}
              alt={'rudolf_hat'}
              className={'menu_thumb'}
              onClick={onClick('hat', 'rudolf')}
            />
          </div>
          <div className={'menu_subitem'}>
            <img
              src={SnowMaidenHat}
              alt={'snowmainden_hat'}
              className={'menu_thumb'}
              onClick={onClick('hat', 'snowmaiden')}
            />
          </div>
        </div>
        <div
          className={'menu_sub sub2'}
          style={{
            display: subMenu === 'sub2' ? 'block' : 'none'
          }}
        >
          <div className={'menu_subitem'}>
            <img
              onClick={onClick('chest', 'santa')}
              src={SantaChest}
              className={'menu_thumb'}
              alt={'chest'}
            />
          </div>
          <div className={'menu_subitem'}>
            <img
              src={ElfChest}
              alt={'elf_chest'}
              className={'menu_thumb'}
              onClick={onClick('chest', 'elf')}
            />
          </div>
          <div className={'menu_subitem'}>
            <img
              src={RudolfChest}
              alt={'rudolf_chest'}
              className={'menu_thumb'}
              onClick={onClick('chest', 'rudolf')}
            />
          </div>
          <div className={'menu_subitem'}>
            <img
              src={SnowMaidenChest}
              alt={'snowmaiden_chest'}
              className={'menu_thumb'}
              onClick={onClick('chest', 'snowmaiden')}
            />
          </div>
        </div>
        <div
          className={'menu_sub sub3'}
          style={{
            display: subMenu === 'sub3' ? 'block' : 'none'
          }}
        >
          <div className={'menu_subitem'}>
            <img
              onClick={onClick('legs', 'santa')}
              src={SantaLegs}
              className={'menu_thumb'}
              alt={'santa_legs'}
            />
          </div>
          <div className={'menu_subitem'}>
            <img
              src={ElfLegs}
              className={'menu_thumb'}
              alt={'elf_legs'}
              onClick={onClick('legs', 'elf')}
            />
          </div>
          <div className={'menu_subitem'}>
            <img
              src={RudolfLegs}
              alt={'rudolf_legs'}
              className={'menu_thumb'}
              onClick={onClick('legs', 'rudolf')}
            />
          </div>
          <div className={'menu_subitem'}>
            <img
              src={SnowMaidenLegs}
              alt={'snowmaiden_legs'}
              className={'menu_thumb'}
              onClick={onClick('legs', 'snowmaiden')}
            />
          </div>
        </div>
        <div className='menu'>
          <div className='menu_item' onClick={onMenuItemClick('sub1')}>
            <img src={SantaHat} alt={'hats'} className={'menu_thumb'} />
          </div>
          <div className='menu_item' onClick={onMenuItemClick('sub2')}>
            <img src={SantaChest} alt={'chests'}  className={'menu_thumb'} />
          </div>
          <div className='menu_item' onClick={onMenuItemClick('sub3')}>
            <img src={SantaLegs}  alt={'legs'} className={'menu_thumb'} />
          </div>
        </div>
      </div>
    </div>
  );
};

export { Menu };
