import { useEffect } from 'react';
import { Image } from 'react-konva';
import useImage  from 'use-image';
import CodyWoEmotion from '../images/Cody_wo_face.png';
import EmotionPositive from '../images/emotion_1.png';
import EmotionNegative from '../images/emotion_2.png';
import { Hat } from './Hat';
import { Chest } from './Chest';
import { Legs } from './Legs';
import { ACTION_TYPE } from '../reducer';

const Cody = ({ canvasSize, state, dispatch }) => {
  const [codyImage] = useImage(CodyWoEmotion);
  const [imageEmotionPositive] = useImage(EmotionPositive);
  const [imageEmotionNegative] = useImage(EmotionNegative);
  const width = 100;
  const height = 150;
  const x = (canvasSize.width / 2) - (width / 2);
  const y = (canvasSize.height / 2) - (height / 2);

  useEffect(() => {
    let timeOut = null;
    if (state.currentEmotion) {
      timeOut = setTimeout(() => {
        dispatch({ type: ACTION_TYPE.SET_CURRENT_EMOTION, payload: null });
      }, 2000);
    }
    return () => timeOut && clearTimeout(timeOut);
  }, [dispatch, state]);

  useEffect(() => {
    if (
      state.currentHat === 'santa'
      && state.currentChest === 'santa'
      && state.currentLegs === 'santa'
    ) {
      dispatch({ type: ACTION_TYPE.SET_CURRENT_SCENE, payload: 'finish' });
    }
  }, [state])

  return (
    <>
      <Image
        x={x}
        y={y}
        image={codyImage}
        width={width}
        height={height}
      />
      {
        state.currentEmotion === 'positive' && (
          <Image
            x={x + 27}
            y={y + 42}
            image={imageEmotionPositive}
            width={34}
            height={18}
          />
        )
      }
      {
        state.currentEmotion === 'negative' && (
          <Image
            x={x + 27}
            y={y + 42}
            image={imageEmotionNegative}
            width={34}
            height={18}
          />
        )
      }
      <Hat currentHat={state.currentHat} x={x} y={y} />
      <Legs currentLegs={state.currentLegs} x={x + 20} y={y+ 120} />
      <Chest currentChest={state.currentChest} x={x + 2} y={y + 70}/>
    </>
  )
};

export { Cody };
