import { useMemo } from 'react';
import useImage from 'use-image';
import { Image } from 'react-konva';
import SantaLegs from '../images/1_3.png';
import ElfLegs from '../images/2_3.png';
import RudolfLegs from '../images/3_3.png';
import SnowmaidenLegs from '../images/4_3.png';



const Legs = ({ currentLegs, x, y }) => {
    const legs = useMemo(() => {
        if (currentLegs === 'santa') return SantaLegs;
        if (currentLegs === 'elf') return ElfLegs;
        if (currentLegs === 'rudolf') return RudolfLegs;
        if (currentLegs === 'snowmaiden') return SnowmaidenLegs;
    }, [currentLegs]);

    const [image] = useImage(legs);

    const hatY = useMemo(() => {
        if (currentLegs === 'rudolf') {
            return y - 8;
        } else {
            return y;
        }
    },[currentLegs, y]);

    return (
      currentLegs &&
        <Image x={x} y={hatY} width={60} height={40} image={image} />
    )
};

export { Legs };
