export const initialState = {
  currentScene: 'start',
  currentHat: null,
  currentChest: null,
  currentLegs: null,
  currentEmotion: null,
};

export const ACTION_TYPE = {
  SET_CURRENT_SCENE: 'SET_CURRENT_SCENE',
  SET_CURRENT_HAT: 'SET_CURRENT_HAT',
  SET_CURRENT_CHEST: 'SET_CURRENT_CHEST',
  SET_CURRENT_LEGS: 'SET_CURRENT_LEGS',
  SET_CURRENT_EMOTION: 'SET_CURRENT_EMOTION',
}

export const reducer = (state, action) => {
  switch (action.type) {
    case ACTION_TYPE.SET_CURRENT_HAT:
      return {
        ...state,
        currentHat: action.payload,
      };

    case ACTION_TYPE.SET_CURRENT_CHEST:
      return {
        ...state,
        currentChest: action.payload,
      };

    case ACTION_TYPE.SET_CURRENT_LEGS:
      return {
        ...state,
        currentLegs: action.payload,
      };

    case ACTION_TYPE.SET_CURRENT_EMOTION:
      return {
        ...state,
        currentEmotion: action.payload,
      };

    case ACTION_TYPE.SET_CURRENT_SCENE:
      return {
        ...state,
        currentScene: action.payload,
      };

    default:
      throw new Error();
  }
}