import useImage from 'use-image';
import { Image } from 'react-konva';
import SantaHat from '../images/1_1.png';
import ElfHat from '../images/2_1.png';
import RudolfHat from '../images/3_1.png';
import SnowmaidenHat from '../images/4_1.png';
import { useMemo } from 'react';


const Hat = ({ currentHat, x, y }) => {
    const hat = useMemo(() => {
        if (currentHat === 'santa') return SantaHat;
        if (currentHat === 'elf') return ElfHat;
        if (currentHat === 'rudolf') return RudolfHat;
        if (currentHat === 'snowmaiden') return SnowmaidenHat;
    }, [currentHat]);

    const [image] = useImage(hat);

    const hatX = useMemo(() => {
        if (currentHat === 'santa') return x;
        if (currentHat === 'elf') return x - 2;
        if (currentHat === 'rudolf') return x + 3;
        if (currentHat === 'snowmaiden') return x + 5;
    },[currentHat, x]);

    const hatY = useMemo(() => {
        if (currentHat === 'santa') return y - 6;
        if (currentHat === 'elf') return y - 21;
        if (currentHat === 'rudolf') return y - 16;
        if (currentHat === 'snowmaiden') return y - 20;
    },[currentHat, y]);

    const hatWidth = useMemo(() => {
        if (currentHat === 'santa') return 80;
        if (currentHat === 'elf') return 80;
        if (currentHat === 'rudolf') return 84;
        if (currentHat === 'snowmaiden') return 80;
    },[currentHat]);

    const hatHeight = useMemo(() => {
        if (currentHat === 'santa') return 50;
        if (currentHat === 'elf') return 65;
        if (currentHat === 'rudolf') return 50;
        if (currentHat === 'snowmaiden') return 50;
    },[currentHat]);

    return (
      currentHat &&
        <Image x={hatX} y={hatY} width={hatWidth} height={hatHeight} image={image} />
    )
};

export { Hat };
