import '../App.css';
import { ACTION_TYPE } from '../reducer';

export const Modal = ({ state, dispatch }) => {
  const handleClick = () => {
    dispatch({type: ACTION_TYPE.SET_CURRENT_SCENE, payload: 'game'})
  };

  return (
    <div className={'modal_container'}>
      <div className={'modal_body'}>
        {
          state.currentScene === 'start' && (
            <>
              <div className={'modal_text'}>
                Привет!
                <br/>
                Помоги мне подобрать
                <br />
                соответвующий наряд
              </div>
              <button onClick={handleClick} className={'modal_button'}>
                Начать
              </button>
            </>
          )
        }
        {
          state.currentScene === 'finish' && (
            <>
              <div className={'modal_text'}>
                Вау! Я отлично выгляжу!
                <br />
                Как настоящий Санта Cloud!
                <br />
                Спасибо!
              </div>
            </>
          )
        }
      </div>
    </div>
  )
}